@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&family=Montserrat);
*{margin:0;padding:0;box-sizing:border-box}body{background:#f7f7f7;font-family:"Montserrat", sans-serif}

.job-apply-button{background-color:#35a7ff;color:white;padding:7px 20px;font-size:15px;border:none;border-radius:4px;margin-top:10px}

.job-card{background:#fff;padding:1rem;margin-bottom:1.2rem;cursor:pointer;border-radius:4px;box-shadow:#ededed}.job-card:hover{border:#ededed solid 1px;transform:scale(1.005)}.job-card .job-title{font-size:1.2rem}.job-card .job-title .job-company{color:#0468bf}.job-card .job-location{color:gray;margin:7px 0}

.job-page{padding:1rem;max-width:730px;margin:auto}.job-page h1{text-align:center;margin:2rem 0;color:#0468bf}.job-page .job-page-loading{text-align:center;margin-top:4rem}.job-page .job-page-loading .job-page-spinner{color:#0468bf}.job-page .job-fetch-error{color:red;text-align:center;margin-top:4rem}

